import React from 'react';

import {merge} from 'lodash';


class GalleryItem extends React.Component {
  static propTypes = { 
    children: React.PropTypes.element,
    classStr: React.PropTypes.string,
    data: React.PropTypes.object.isRequired,
    show: React.PropTypes.bool.isRequired,
    style: React.PropTypes.object,
  }
  constructor(props) {
    super(props);
    this.displayName = 'GalleryItem';
  }
  render() {
    const {
      data,
      show,
      style = {},
      classStr = '',
      children,
    } = this.props;
    const {
      className,
      text = [],
    } = data;
    const _style = {
      display: show ? 'block' : 'none',
    };
    merge(_style, style);
    return (
      <div className={`gallery-item ${className} ${classStr}`} style={_style} >
        {children}
        <div className={'gallery-item-box'}>
          <div className={'gallery-item-table'}>
            {text.map((o, i) => {
              const {
                key,
                value,
              } = o;
              return (
                <div className={'gallery-item-tr'} key={i}>
                  <div className={'gallery-item-th'}>{key}</div>
                  <div className={'gallery-item-td'} dangerouslySetInnerHTML={{__html: value}} />
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default GalleryItem;



