import React from 'react';

class MenuItem extends React.Component {
  static propTypes = {
    currentItem: React.PropTypes.string.isRequired,
    name: React.PropTypes.string,
    value: React.PropTypes.string.isRequired,
  }
  static contextTypes = {
    changeModule: React.PropTypes.func.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'MenuItem';
  }
  _clickHandle = (value) => {
    this.context.changeModule(value);
  }
  render() {
    const {
      currentItem,
      name,
      value,
    } = this.props;

    return (
      <div 
        className={`menu-item ${value === currentItem ? 'current' : ''}`} 
        onClick={this._clickHandle.bind(this, value)} 
      >
        <span>{name || value}</span>
      </div>
    );
  }
}

export default MenuItem;



