import React from 'react';
const PropTypes = React.PropTypes;

const colorMap = {
  'A': '#d40f44',
  'B': '#5090ce',
  'C': '#3d6f90',
  'D': '#f6c8d3',
  'E': '#588721',
  'F': '#98012e',
  'G': '#dae6c0',
  'H': '#fee89d',
  'I': '#a5d8f7',
  'J': '#b4975b',
  'K': '#e2e479',
  'L': '#afbd20',
  'M': '#a1c5db',
  'N': '#bed7a0',
  'O': '#07203f',
  'P': '#cbe0d9',
  'Q': '#72b099',
  'R': '#ffd254',
  'S': '#018577',
  'T': '#569abd',
  'U': '#e7971c',
  'V': '#f1f4a3',
  'W': '#8d8b8c',
  'X': '#b9afa5',
  'Y': '#d6cfc7',
  'Z': '#eee9e6',
};

const lengthMap = {
  'A': 60,
  'E': 60,
  'I': 60,
  'O': 60,
  'U': 60,
};

class ColorLetter extends React.Component {
  static propTypes = {
    word: PropTypes.string.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'ColorLetter';
  }
  render() {
    const {
      word: _word,
    } = this.props;
    const word = _word.toLocaleUpperCase();
    const render = [];
    for ( let i = 0, length = word.length; i < length; i++ ) {
      const letter = word[i];
      const style = {
        background: colorMap[letter] || 'transparent',
        height: lengthMap[letter] || 34,
      };
      render.push(
        <div className={'word-box'} key={i} >
          <div className={'word-area'}><div className={'word-item'} style={style} /></div>
          <span>{letter}</span>
        </div>
      );
    }

    return <div className={'word-group'}>{render}</div>;
    
  }
}

export default ColorLetter;



