import React from 'react';
const PropTypes = React.PropTypes;

import {minBy, maxBy} from 'lodash';

class TimeLine extends React.Component {
  static propTypes = {
    data: PropTypes.array.isRequired,
    title: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'TimeLine';
  }
  render() {
    const {
      data = [],
      title,
      type,
    } = this.props;

    const _timeStart = minBy(data, (o) => o.timeStart);
    const _timeEnd = maxBy(data, (o) => o.timeEnd);
    const _start = _timeStart ? +_timeStart.timeStart.slice(0, 4) : 0;
    const _startM = type !== 'year' ? +_timeStart.timeStart.slice(4, 6) : 1;
    const _end = _timeEnd ? +_timeEnd.timeEnd.slice(0, 4) : 0;
    const _endM = type !== 'year' ? +_timeEnd.timeEnd.slice(4, 6) : 12;
    const length = (_end - _start) * 12 + _endM - _startM + 1;
    /**
     * 建立时间轴
     * @type {Array}
     */
    const timeLine = [];
    for (let i = _start; i <= _end; i++) {
      if (type === 'year') {
        timeLine.push(<div className={'timeLine-item timeLine-year'} key={`year_${i}`} >{i}</div>);
      } else {
        for (let j = ( i === _start ? _startM : 1 ); j <= (i === _end ? _endM : 12); j++) {
          const month = j === 1 || j === 7 ? `${i}.${j}` : j;
          timeLine.push(<div className={'timeLine-item timeLine-month'} key={`month_${i}_${j}`} >{month}</div>);
        }
      }
    }

    /**
     * 建立事件
     */
    const timeEvent = [];
    for (let i = 0, _length = data.length; i < _length; i++) { 
      const {
        timeStart,
        timeEnd,
        type,
        text,
        title,
      } = data[i];
      const itemStart = (+timeStart.slice(0, 4) - _start) * 12 + +timeStart.slice(4, 6) - _startM + 1;
      const itemEnd = (_end - timeEnd.slice(0, 4)) * 12 - timeEnd.slice(4, 6) + _endM + .05 ;
      const itemStyle = {
        top: `${itemStart / length * 100}%`,
        bottom: `${itemEnd / length * 100}%`,
      };
      timeEvent.push(
        <div 
          className={`timeLine-event event-${type}`} 
          style={itemStyle}
          key={`timeEvent_${i}`}
        >
          <div className={'timeLine-event-item'}>
            <div className={'timeLine-event-title'}>{title}</div>
            <div className={'timeLine-event-text'}>{text}</div>
          </div>
        </div>
      );
    }

    return (
      <div className={'timeLine-container'}>
        <h2>{title}</h2>
        <div className={'timeLine-area'}>
          <div className={'timeLine-box'}>{timeLine}</div>
          {timeEvent}
        </div>
      </div>
    );
  }
}

export default TimeLine;



