import React from 'react';
import ColorLetter from 'components/plugins/ColorLetter';

import {getStyle, animation} from 'tool/animationManager';

import GalleryLeft from 'components/GalleryLeft';
import GalleryShadowLeft from 'components/GalleryShadowLeft';
import GalleryShadowRight from 'components/GalleryShadowRight';
import GalleryBox from 'components/GalleryBox';

/**
 * 动画
 * @type {Array}
 */ 
const ANIMATION = [
  {
    start: 0,
    length: 300,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'translate({s1}%, 0)', 
      transform: 'translate({s1}%, 0)', 
    },
    prams: {
      s1: {
        start: -25,
        count: 25,
      },
    },
  },
];

class Gallery extends React.Component {
  static propTypes = { 
    current: React.PropTypes.bool.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'Gallery';
    this.state = {
      step: 0,
    };
  }
  _clickHandle = () => {
    if (this.state.step) return;
    animation(0, 400, 2000, (step) => {
      this.setState({
        step: step,
      });
    });   
  }
  _closeHandle = () => {
    if (this.state.step !== 400) return;
    animation(400, 0, 2000, (step) => {
      this.setState({ 
        step: step,
      });
    });   
  }
  render() {
    const {
      current,
    } = this.props;
    const {
      step,
    } = this.state;
    const animtClass = ['gallery'];
    
    const animtStyle = getStyle(step, ANIMATION, animtClass);
    return (
      <div className={`letter-layout ${current ? 'page-in' : 'page-out'}`}>
        <div className={animtClass.join(' ')} style={animtStyle} >
          <GalleryLeft parentStep={step} clickHandle={this._clickHandle} />
          <GalleryShadowLeft parentStep={step} />
          <GalleryShadowRight parentStep={step} />
          <GalleryBox parentStep={step} />
          <div className={'gallery-r'} onClick={this._closeHandle}>
            <div className={'gallery-last'}>
              <div className={'gallery-circle'}>
              </div>
              <ColorLetter word={'from three'} />
            </div>
          </div>
          <div className={'gallery-r gallery-shadow'}>
            <div className={'gallery-last'}>
              <div className={'gallery-circle'}>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Gallery;



