import React from 'react';
const PropTypes = React.PropTypes;

import {getStyle} from 'tool/animationManager';
import ColorLetter from 'components/plugins/ColorLetter';
import LetterTop from 'components/LetterTop';
import LetterPaper from 'components/LetterPaper';
/**
 * 动画
 * @type {Array}
 */
const ANIMATION = [
  {
    start: 0,
    length: 500,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'translate3d(0,-100%, {s4}px) rotateX({s1}deg) rotateY({s2}deg) rotateZ({s3}deg)', 
      transform: 'translate3d(0,-100%, {s4}px) rotateX({s1}deg) rotateY({s2}deg) rotateZ({s3}deg)', 
    },
    prams: {
      s1: {
        start: 45,
        count: -45,
      },
      s2: {
        start: 170,
        count: 10,
      },
      s3: {
        start: -12,
        count: 12,
      },
      s4: {
        start: -1500,
        count: 500,
      },
    },
  },
  /*{
    start: 500,
    length: 300,
    style: {
      transform: 'translate3d(0,-100%, -1000px)', 
    },
  },*/
  {
    start: 500,
    length: 1300,
    bezier: 'bounce',
    style: {
      WebkitTransform: 'translate3d(0,-100%, -1000px)  rotateY({s1}deg) ', 
      transform: 'translate3d(0,-100%, -1000px)  rotateY({s1}deg) ', 
    },
    className: [
      {
        percent: 50,
        type: 'in',
        name: 'in',
      },
    ],
    prams: {
      s1: {
        start: 180,
        count: -180,
      },
    },
  },
  {
    start: 1800,
    length: 800,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'translate3d(0,{s2}%, {s1}px)', 
      transform: 'translate3d(0,{s2}%, {s1}px)', 
    },
    prams: {
      s1: {
        start: -1000,
        count: 1000,
      },
      s2: {
        start: -100,
        count: 100,
      },
    },
  },
];

class Letter extends React.Component {
  static propTypes = {
    current: PropTypes.bool.isRequired,
    step: PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'Letter';
  }
  render() {
    const {
      step,
      current,
    } = this.props;
    const letterClass = ['letter'];
    
    const letterStyle = getStyle(step, ANIMATION, letterClass);

    return (
      <div className={`letter-layout ${current ? 'page-in' : 'page-out'}`}>
        <div className={letterClass.join(' ')} style={letterStyle}>
          <div className="letter-back">
            <div className="letter-back-logo">
              <ColorLetter word="from three" />
            </div>
          </div>
          <div className="letter-positive">
            <div className="letter-positive-bg abs"></div>
            <LetterPaper step={step} />
            <div className="letter-positive-r abs"></div>
            <div className="letter-positive-l abs"></div>
            <div className="letter-positive-b abs"></div>
            <LetterTop step={step} />
          </div>
        </div>
      </div>
    );
  }
}

export default Letter;



