import React from 'react';

import {getStyle} from 'tool/animationManager';

import resume from 'config/resume';
const {workExprienceEx} = resume;

import GalleryItem from 'components/GalleryItem';
import {merge} from 'lodash';

import {animation} from 'tool/animationManager';
import bezierLine from 'tool/bezierLine';
const line = bezierLine.easeOutQuint;
/**
 * 动画
 * @type {Array}
 */ 
const ANIMATION = [
  {
    start: 0,
    length: 400,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'rotateY({s1}deg)', 
      transform: 'rotateY({s1}deg)',
    },
    className: [
      {
        percent: 50,
        type: 'in',
        name: 'in',
      },
      {
        percent: 100,
        type: 'in',
        name: 'done',
      },
    ],
    prams: {
      s1: {
        start: 180,
        count: -180,
      },
    },
  },
];
class GalleryBox extends React.Component {
  static propTypes = { 
    parentStep: React.PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'GalleryBox';
    this.state = {
      /**
       * 页码
       * @type {Number}
       */
      pageNum: 0,
      count: workExprienceEx.length,
      /**
       * 翻页参数
       * @type {Boolean}
       */
      isAnimate: false,
      isDown: false,
      sx: 0,
      sy: 0,   
      x: 0,
      y: 0,
      arrow: true,
    };
  }
  /**
   * 鼠标按下事件 
   * @yansanmu github.com/yansm
   * @DateTime 2016-04-18T16:59:51+0800
   * @param    {[type]}                 e [description]
   * @return   {[type]}                   [description]
   */
  _mouseDownHandle = (e) => {
    const {
      parentStep,
    } = this.props;
    if (parentStep < 400) return;
    const {
      isAnimate,
      count,
      pageNum,
    } = this.state;
    if (isAnimate) return;
    const {
      left,
    } = this.refs.galleryArea.getBoundingClientRect();
    
    const {
      pageX,
      pageY,
    } = e;
    const arrow = pageX - left > 1080 / 2;
    if (arrow && pageNum >= count - 1) return;
    else if (!arrow && pageNum <= 0) return;

    const nowPageNum = arrow ? pageNum + 1 : pageNum - 1;

    this.setState({
      sx: pageX,
      x: pageX,
      sy: pageY,
      y: pageY,
      isAnimate: true,
      isDown: true,
      arrow: arrow,
      reArrow: true,
      pageNum: nowPageNum,
    });
  }
  
  /**
   * 鼠标移动事件
   * @yansanmu github.com/yansm
   * @DateTime 2016-04-18T17:00:23+0800
   * @param    {[type]}                 e [description]
   * @return   {[type]}                   [description]
   */
  _mouseMoveHandle = (e) => {
    const {
      isDown,
      x,
      arrow,
    } = this.state;
    if (!isDown) return;
    const {
      pageX,
      pageY,
    } = e;
    this.setState({
      x: pageX,
      y: pageY,
      reArrow: !arrow ? x - pageX > 0 : x - pageX < 0,
    });
  }
  /**
   * 鼠标抬起事件
   * @yansanmu github.com/yansm
   * @DateTime 2016-04-18T17:00:15+0800
   * @return   {[type]}                 [description]
   */
  _mouseUpHandle = () => {
    const {
      isAnimate,
    } = this.state;
    if (!isAnimate) return;
    this.setState({
      isDown: false,
    });
    const {
      arrow,
      x,
      y,
      sx,
      sy,
      pageNum,
      reArrow,
    } = this.state;
    const a = x - sx;
    const b = y - sy;
    const xEnd = reArrow ? 0 : arrow ? -540 : 540;
    const pageNow = xEnd ? pageNum : arrow ? pageNum - 1 : pageNum + 1;
    const yEnd = 0;
    const xCount = Math.abs(xEnd - a);
    const yCount = Math.abs(yEnd - b);
    const steps = yCount > xCount ? yCount : xCount;
    animation(0, steps, steps * 1.6, (step) => { 
      if (steps === step) {
        this.setState({ 
          pageNum: pageNow,
          isAnimate: false,
        });
      }
      if (!steps) return;
      let nx = line(step / steps) * xCount;
      let ny = line(step / steps) * yCount ;
      ny = b > 0 ? y - ny : y + ny;
      nx = !xEnd ? arrow ? x + nx : x - nx : !arrow ? x + nx : x - nx;
      this.setState({
        x: nx,
        y: ny,
      });
    });  

  }

  render() {
    const {
      parentStep,
    } = this.props;
    const {
      pageNum,
      isAnimate,
    } = this.state;
    
    const animtClass = ['gallery-box'];
    
    const galleryList = [];
    if (parentStep >= 400) {
      for (let i = 0, length = workExprienceEx.length; i < length; i++) {
        galleryList.push(
          <GalleryItem data={workExprienceEx[i]} key={`galleryItem_${i}`} show={pageNum === i} />
        );
      }
    }
    const animtStyle = getStyle(parentStep, ANIMATION, animtClass);
    const currentPage = <GalleryItem data={workExprienceEx[pageNum]} show={true} />;
    /**
     * 翻页效果
     */
    let page = null;
    let shadowP = null;
    if (isAnimate) {
      const {
        arrow,
        x,
        y,
        sx,
        sy,
      } = this.state;
      let a = x - sx;
      let b = y - sy;
      const pageStyle = {};
      const currentStyle = {};
      const nextStyle = {};
      const shadowInStyle = {};
      const shadowStyle = {};
      /**
       * 横向位移范围 0~540
       * @yansanmu github.com/yansm
       * @DateTime 2016-04-13T16:33:52+0800
       */
      if ((arrow && a > 0) || (!arrow && a < 0)) {
        a = 0;
      }
      if (arrow && a < -540) a = -540;
      else if (!arrow && a > 540) a = 540;
      /**
       * [bMax description]
       * @type {[type]}
       */
      const bMax = Math.sqrt((1 - Math.pow(a, 2) / 502841.8687167357) * Math.pow(540, 2)) - 350;
      if (b > bMax) b = bMax;
      else if (b < -bMax) b = -bMax;


      const c = Math.sqrt(Math.pow(a, 2) + Math.pow(b, 2));

      /**
       * 蒙版位移
       */
      pageStyle[arrow ? 'right' : 'left'] = 0;
      pageStyle.WebkitTransformOrigin = pageStyle.transformOrigin
        = `${arrow ? 87.5 : 12.5}% 50%`;
      /**
       * 当前页
       */
      
      currentStyle.top = '50%';
      currentStyle.marginTop = '-325px';
      currentStyle[arrow ? 'right' : 'left'] = 0;
      currentStyle[arrow ? 'left' : 'right'] = 'auto';
      /**
       * 所翻页
       */
      nextStyle.top = '50%';
      nextStyle.marginTop = '-325px';
      nextStyle[arrow ? 'right' : 'left'] = '-25%';
      nextStyle[arrow ? 'left' : 'right'] = 'auto';

      /**
       * 影子页
       */
      shadowInStyle[!arrow ? 'right' : 'left'] = 0;
      shadowInStyle.WebkitTransformOrigin = shadowInStyle.transformOrigin
        = `${!arrow ? 87.5 : 12.5}% 50%`;
      /**
       * 影子范围
       */
      let shadowBlur;
      if (arrow) shadowBlur = 540 + a;
      else shadowBlur = 540 - a;
      if (shadowBlur > 540 - 55) shadowBlur = 540 - shadowBlur;
      else if (shadowBlur > 55) shadowBlur = 55;
      shadowStyle.boxShadow = `0 0 100px ${shadowBlur - 55}px rgba(0, 0, 0, .5)`;

      if (a !== 0) {
        const sinP = (arrow ? -a : a) / c;
        const cosP = (arrow ? -b : b) / c;
        pageStyle.WebkitTransform = pageStyle.transform 
          = `matrix(${sinP}, ${cosP}, ${-cosP}, ${sinP}, ${a}, ${b})`;
        currentStyle.WebkitTransform = currentStyle.transform 
          = `matrix(${sinP}, ${-cosP}, ${cosP}, ${sinP}, ${-a / sinP}, 0)`;
        nextStyle.WebkitTransform = nextStyle.transform 
          = `matrix(${sinP}, ${cosP}, ${-cosP}, ${sinP}, ${a / sinP}, 0)`;
        shadowInStyle.WebkitTransform = shadowInStyle.transform 
          = `matrix(${sinP}, ${-cosP}, ${cosP}, ${sinP}, ${-a}, ${b}`;
      }
      const currentP 
        = <GalleryItem data={workExprienceEx[arrow ? pageNum - 1 : pageNum + 1]} show={true} style={currentStyle}/>;
      const nextP = (
        <GalleryItem 
          data={workExprienceEx[pageNum]} 
          show={true} 
          style={nextStyle} 
          classStr={'has-shadow'} 
        >
          <div className={'page-shadow-area in'} style={merge({}, shadowInStyle, shadowStyle)} />
        </GalleryItem>
      );
      /**
       * 阴影
       * @type {[type]}
       */
      shadowP = (
        <div className={'page-shadow'}>
          <div className={'page-shadow-area out'} style={merge({}, pageStyle, shadowStyle)} >
            <div className={'page-shadow'} style={merge({}, nextStyle, shadowStyle)} />
          </div>
        </div>
      );
      page = (
        <div className={'page-area'} style={pageStyle}>
          {currentP}
          {nextP}
        </div>
      );
    } 

    return (
      <div className={animtClass.join(' ')}>
        <div 
          className={'gallery-area'}
          ref={'galleryArea'}
          onMouseDown={this._mouseDownHandle}
          onMouseUp={this._mouseUpHandle}
          onMouseLeave={this._mouseUpHandle}
          onMouseMove={this._mouseMoveHandle}
        >
          {galleryList}
          {page}
          {shadowP} 
        </div>
        <div className={'gallery-box-l'} style={animtStyle} >
          {currentPage}
        </div>
        <div className={'gallery-box-r'}>
          {currentPage}
        </div>
      </div>
    );
  }
}

export default GalleryBox;



