import React from 'react';

import {getStyle} from 'tool/animationManager';

/**
 * 动画
 * @type {Array}
 */ 
const ANIMATION = [
  {
    start: 0,
    length: 400,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'rotateY({s1}deg)', 
      transform: 'rotateY({s1}deg)',
    },
    className: [
      {
        percent: 50,
        type: 'in',
        name: 'in',
      },
    ],
    prams: {
      s1: {
        start: 180,
        count: -180,
      },
    },
  },
];


class GalleryLeft extends React.Component {
  static propTypes = { 
    clickHandle: React.PropTypes.func,
    parentStep: React.PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'GalleryLeft';
  }
  render() {
    const {
      parentStep,
      clickHandle,
    } = this.props;
    
    const animtClass = ['gallery-l'];
    
    const animtStyle = getStyle(parentStep, ANIMATION, animtClass);
    return (
      <div className={animtClass.join(' ')} style={animtStyle} onClick={clickHandle}>
        <div className={'gallery-positive-shadow'}>
        </div>
        <div className={'gallery-positive'}>
          <div className={'gallery-transparent'}></div>
        </div>
        <div className={'gallery-front'}>
          <h1>Work Exprience</h1>
          <h1>of</h1>
          <h1>Yan Sanmu</h1>
        </div>
      </div>
    );
  }
}

export default GalleryLeft;



