/**
 * 框架组件
 */
import React from 'react';
const PropTypes = React.PropTypes;

import Letter from 'components/Letter';
import Gallery from 'components/Gallery';
import Menu from 'components/plugins/Menu';

const CONFIG = {
  v: .5,
  mv: 12,
};

class Layout extends React.Component {
  static childContextTypes = {
    changeModule: PropTypes.func.isRequired,
    canScroll: PropTypes.func.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'Layout';
    this.state = {
      canScroll: false,
      step: 0,
      end: 2400,
      index: 0,
      speed: 0,
      arrow: true,
      arrowFlag: true,
      current: 'resume',
    };
    
  }
  getChildContext() {
    return {
      changeModule: this._changeModule,
      canScroll: this._canScroll,
    };
  }
  componentDidMount() {
    this.initInterval = setInterval(() => {
      const {
        step,
        end,
      } = this.state;
      if (step >= end) {
        clearInterval(this.initInterval);
      }
      this.setState({
        step: step + 6,
      });
    }, 1);
  }
  _changeModule = (value) => {
    this.setState({
      current: value,
    });
  }
  _canScroll = (flag) => {
    this.setState({
      canScroll: flag,
    });
  }
  _onScroll = (e) => {
    if (!this.state.canScroll) return;
    const arrow = e.deltaY > 0;
    const {
      index,
      arrow: pArrow,
      current,
    } = this.state;
    if (current !== 'resume') return;
    this.setState({
      arrow: arrow,
      //当方向与之前不相相同且计数不为0时，判定为改变方向
      arrowFlag: arrow === pArrow || index === 0,
      index: index - 10,
    });
    if (!this.interval) {
      const {
        v,
        mv,
      } = CONFIG;
      this.interval = setInterval(() => {
        const {
          arrow,
          arrowFlag,
          end,
        } = this.state;
        let {
          speed,
          step,
          index,
        } = this.state;
        index++;
        /**
         * 当滚轮方向为反方向时， 立即停止
         * @yansanmu github.com/yansm
         * @DateTime 2016-03-30T16:02:46+0800
         */
        if (!arrowFlag) {
          speed = 0;
          index = 0;
          clearInterval(this.interval);
          this.interval = null;
        } else
        /**
         * 在阙值范围内  保持匀速
         * @yansanmu github.com/yansm
         * @DateTime 2016-03-28T15:55:30+0800
         */
        if (index < 5) {
          speed = arrow ? mv : -mv;
        } else 
        /**
         * 在阙值范围外  减速
         * @yansanmu github.com/yansm
         * @DateTime 2016-03-28T15:56:11+0800
         */
        if (index >= 5) {
          if (arrow && speed > 0) speed -= v;
          else if (!arrow && speed < 0) speed += v;
          else {
            speed = 0;
            index = 0;
            clearInterval(this.interval);
            this.interval = null;
          }
        }
        step += speed;
        if (step >= end) {
          this._canScroll(false);
        }
        this.setState({
          index: index,
          speed: speed,
          step: step < 0 ? 0 : step > end ? end : step,
        });
      }, 25);
    }
  }
  render() {
    const {
      step,
      current,
    } = this.state;
    return (
      <div 
        className="layout" 
        onWheel={this._onScroll}
        ref="layout"
      >
        <Letter step={step} current={current === 'resume'} />
        <Gallery current={current === 'workExprience'} />
        <Menu currentItem={current} />
      </div>
    );
  }
}

export default Layout;




