import React from 'react';
const PropTypes = React.PropTypes;

import {getStyle} from 'tool/animationManager';
import ColorLetter from 'components/plugins/ColorLetter';

/**
 * 动画
 * @type {Array}
 */
const ANIMATION = [
  {
    start: 1900,
    length: 400,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'rotateX({s1}deg)', 
      transform: 'rotateX({s1}deg)', 
    },
    className: [
      {
        percent: 100,
        type: 'in',
        name: 'open',
      },
      {
        percent: 50,
        type: 'in',
        name: 'in',
      },
    ],
    prams: {
      s1: {
        start: 0,
        count: -180,
      },
    },
  },
];

class LetterTop extends React.Component {
  static propTypes = {
    step: PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'LetterTop';
  }
  render() {
    const {
      step,
    } = this.props;
    const letterClass = ['letter-positive-t', 'abs'];
    const letterStyle = getStyle(step, ANIMATION, letterClass);
    return (
      <div className={letterClass.join(' ')} style={letterStyle} >
        <div className={'abs'} ><ColorLetter word={'a curriculum vitae from yansm'} /></div>
      </div>
    ); 
  }
}

export default LetterTop;



