import React from 'react';
import MenuItem from 'components/plugins/MenuItem';

class Menu extends React.Component {
  static propTypes = {
    currentItem: React.PropTypes.string.isRequired,
  }
  
  constructor(props) {
    super(props);
    this.displayName = 'Menu';
  }
  render() {
    const {
      currentItem,
    } = this.props;
    return (
      <div className={'menu'}>
        <MenuItem value={'resume'} name={'resume'} currentItem={currentItem} />
        <MenuItem value={'workExprience'} name={'work exprience'} currentItem={currentItem} />
      </div>
    );
  }
}

export default Menu;



