import React from 'react';
const PropTypes = React.PropTypes;

import ColorLetter from 'components/plugins/ColorLetter';
import TimeLine from 'components/plugins/TimeLine';
import ResumeBox from 'components/plugins/ResumeBox';

import resume from 'config/resume';


class LetterPaper extends React.Component {
  static propTypes = {
    step: PropTypes.number.isRequired,
  }
  static contextTypes = {
    changeModule: PropTypes.func.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'LetterPaper';
    this.state = {
      heiht: 0,
      windowHeight: 0,
    };
  }
  render() {
    const {
      step,
    } = this.props;
    const letterClass = ['letter-paper-layout'];
    let layoutStyle = {};
    let boxStyle = {};
    if (step >= 2400) {
      const height = this.refs.letterPaperLayout.offsetHeight;
      const windowHeight = document.body.offsetHeight;
      layoutStyle = {
        bottom: `${windowHeight + 600 - height}`,
      };
      boxStyle = {
        top: `${windowHeight + 632 - height}`,
      };
    }
    return (
      <div className={letterClass.join(' ')} ref={'letterPaperLayout'} style={layoutStyle}>
        <div className={'letter-paper-scroll'}>
          <div className={'letter-paper-box'} ref={'letterPaper'} style={boxStyle}>
            <div className={'letter-paper-top'}>
              <ColorLetter word={'from three'} />
              <div className={'letter-paper-r'}>
                {'yansanmu`s'} <span className={'b'}>c</span>urriculum <span className={'b'}>v</span>itae 
              </div>
            </div> 
            <div className={'resume-container'}>
              <div className={'resume-l'}>
                <TimeLine type={'year'} data={resume.education} title="education" />
                <TimeLine type={'mouth'} data={resume.workExprience} title="work exprience" />
              </div>
              <div className={'resume-r'}>
                <ResumeBox data={resume.aboutMe} title="about me" />
                <ResumeBox data={resume.expect} title="expect" />
                <ResumeBox data={resume.kills} title="kills" />
                <ResumeBox data={resume.selfEvaluation} title="self-evaluation" />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LetterPaper;



