import bezierLine from 'tool/bezierLine';
import {remove, find} from 'lodash';

/**
 * 根据step 获得当前style以及classlist
 * @yansanmu github.com/yansm
 * @DateTime 2016-04-08T14:26:30+0800
 * @param    {[type]}                 step        [description]
 * @param    {[type]}                 animate     [description]
 * @param    {Array}                  letterClass [description]
 * @return   {[type]}                             [description]
 */
export function getStyle(step, animate, letterClass = []) {
  let letterStyle = null;
  for (const item of animate) {
    const {
      start,
      length,
      bezier,
      className,
    } = item;
    if (step >= start && step <= start + length ) {
      let percent = (step - start) / length;
      if (bezier && bezierLine[bezier]) {
        percent = bezierLine[bezier](percent);
      }
      const {
        style,
        prams = {},
      } = item;
      let finalStyle = JSON.stringify(style);
      for (const key in prams) {
        const t = prams[key];
        const {
          start: tStart,
          count: tCount,
        } = t;
        const reg = new RegExp(`{${key}}`, 'g');
        finalStyle = finalStyle.replace(reg, tStart + tCount * percent);
      }
      letterStyle = JSON.parse(finalStyle);
      if (className) {
        for (const item of className) {
          const {
            percent: _percent,
            type,
            name,
          } = item;
          if (percent >= _percent / 100) {
            if (type === 'in') {
              if (!find(letterClass, (o) => o === name)) letterClass.push(name);
            } else {
              remove(letterClass, (o) => o === name);
            }
          }
        }
      }
    } else 
    /**
     * 当step 大于当前animate参数
     * @yansanmu github.com/yansm
     * @DateTime 2016-03-29T15:15:03+0800
     */
    if (step >= start + length && className) {
      for (const item of className) {
        const {
          type,
          name,
        } = item;
        if (type === 'in') {
          if (!find(letterClass, (o) => o === name)) letterClass.push(name);
        } else {
          remove(letterClass, (o) => o === name);
        }
      }
    }
  }
  /**
   * 如果letterStyle为空
   * @yansanmu github.com/yansm
   * @DateTime 2016-03-29T17:23:37+0800
   */
  if (!letterStyle && animate.length) {
    if (step < animate[0].start) {
      return getStyle(animate[0].start, animate, letterClass);
    } else {
      const item = animate[animate.length - 1];
      return getStyle(item.start + item.length, animate, letterClass);
    }
  }
  return letterStyle;
}

/**
 * 动画帧数
 * @yansanmu github.com/yansm
 * @DateTime 2016-04-08T14:27:01+0800
 * @param    {[type]}                 start    [description]
 * @param    {[type]}                 end      [description]
 * @param    {[type]}                 time     [description]
 * @param    {Function}               callback [description]
 * @return   {[type]}                          [description]
 */
export function animation(start, end, time, callback) {
  const length = end - start;
  const step = !time ? 0 : length / time * 25;
  let _start = start;
  let i = 0;
  const timeOut = function() {
    setTimeout(() => {
      _start += step;
      if ((length > 0 && _start >= end) || (length < 0 && _start <= end) || !length)
        callback(end);
      else {
        callback(_start);
        timeOut();
      }
      i++;
    }, 25);
  };

  timeOut();
}



