import React from 'react';
const PropTypes = React.PropTypes;

class ResumeBox extends React.Component {
  static propTypes = {
    data: PropTypes.array.isRequired,
    title: PropTypes.string.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'ResumeBox';
  }
  render() {
    const {
      title,
      data = [],
    } = this.props;
    const render = [];

    for (let i = 0, length = data.length; i < length; i++) {
      const item = data[i];
      if (typeof item === 'string') {
        render.push(<p key={`p_${i}`} dangerouslySetInnerHTML={{__html: item}} />);
      } else {
        const {
          key,
          value,
        } = item;
        if (typeof value === 'string') {
          render.push(
            <div className={'article-table'} key={`p_${i}`}>
              <div className={'article-l'}>{key}</div>
              <div className={'article-r'}>
                <span dangerouslySetInnerHTML={{__html: value}} />
              </div>
            </div>
          );
        } else {
          const listRender = [];
          for (let j = 0, vLength = value.length; j < vLength; j++) {
            const {
              name,
              num,
            } = value[j];
            listRender.push(
              <div className={'article-table'} key={`p_${i}_${j}`}>
                <div className={'article-l'}>{name}</div>
                <div className={'article-r'}>
                  <div className={'article-dnm'}><div className={'article-mlc'} style={{width: `${num}%`}} ></div></div>
                </div>
              </div>
            );
          }

          render.push(
            <div className={'article-p'} key={`p_${i}`}>
              <div className={'article-title'}>{key}</div>
              {listRender}
            </div>
          );
        }
        
      }
    }

    return (
      <div className={'article-box'} >
        <h2>{title}</h2>
        <div className={'article-container'} >
          {render}
        </div>
      </div>
    );
  }
}

export default ResumeBox;



