import React from 'react';

import {getStyle} from 'tool/animationManager';

/**
 * 动画
 * @type {Array}
 */ 
const ANIMATION = [
  {
    start: 0,
    length: 400,
    bezier: 'easeOutQuint',
    style: {
      WebkitTransform: 'rotateY({s1}deg)', 
      transform: 'rotateY({s1}deg)',
      opacity: '{s2}',
    },
    className: [
      {
        percent: 50,
        type: 'in',
        name: 'in',
      },
    ],
    prams: {
      s1: {
        start: 180,
        count: -180,
      },
      s2: {
        start: 2,
        count: -2,
      },
    },
  },
];


class GalleryShadowLeft extends React.Component {
  static propTypes = { 
    parentStep: React.PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'GalleryShadowLeft';
  }
  render() {
    const {
      parentStep,
    } = this.props;
    
    const animtClass = ['gallery-shadow-l'];
    
    const animtStyle = getStyle(parentStep, ANIMATION, animtClass);
    return (
      <div className={animtClass.join(' ')} style={animtStyle}>
        <div className={'gallery-positive-shadow'}>
        </div>
      </div>
    );
  }
}

export default GalleryShadowLeft;



